import BackendUrl from "./BackendUrl"

export default function WaterApiList() {
    let baseUrl = BackendUrl;

    let token = window.localStorage.getItem('citizen_token')
    const header = {
        headers:
        {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
        }
    }

    // window.sessionStorage.setItem("name", "Raja Ram Mohan Malvia")

    let apiList = {
        header: header,

        api_ulbList: `${baseUrl}/api/get-all-ulb`, //GET
        api_wardList: `${baseUrl}/api/get-all-ulb-wards`,
        api_userProfileDetails: `${baseUrl}/api/my-profile-details`,

        api_NewWaterConnection: `${baseUrl}/api/water/application/apply-new-connection`,

        api_getSafHoldingDetails: `${baseUrl}/api/water/search-holding-saf`,

        api_citizenApplications: `${baseUrl}/api/water/citizenApplications`, // Method => Post | Fetch all applied list of water connection
        api_citizenApprovedApplications: `${baseUrl}/api/water/consumer/get-listed-details`, // 

        api_rzpWaterOrderId: `${baseUrl}/api/water/Razorpay-Orderid`, // Method => POST | generate Razorpay order id for  water payment
        api_getTranNo: `${baseUrl}/api/water/getTranNo`, // Method => POST | Fetch Data by OrderId and PaymentId

        api_paymentReceipt: `${baseUrl}/api/water/generate-payment-receipt`, // Method => POST | Fetch Data for generate payment receipt
        api_waterConsumerPaymentReceipt: `${baseUrl}/api/water/consumer/demand/generate-payment-receipt`, // Method => POST | Fetch Data for Water Demand Payment

        api_waterApplicationDelete: `${baseUrl}/api/water/application/delete `, // Method => POST 
        api_waterViewPendingApplication: `${baseUrl}/api/water/application/get-by-id `, // Method => POST 

        api_waterViewDocumetTobeuploaded: `${baseUrl}/api/water/citizen/get-doc-list`, // Method => POST 
        api_waterUploadDoc: `${baseUrl}/api/water/upload-document`, // Method => POST 

        // api_waterFinalFormSubmit: `${baseUrl}/api/water/final-submit-application`, // Method => POST 

        api_waterApprovedApplicationDetails: `${baseUrl}/api/water/consumer/get-listed-details`,
        api_waterConsumerPaymentHistory: `${baseUrl}/api/water/consumer/get-payment-history`,
        api_waterListDemand: `${baseUrl}/api/water/consumer/list-demand`,
        api_getHoldingSafOfLogin: `${baseUrl}/api/property/citizen-holding-saf`,

        api_waterDemandCalculation: `${baseUrl}/api/water/consumer/calculate-month-demand`,
        api_waterDemandPayment: `${baseUrl}/api/water/consumer/offline-demand-payment`,

    }

    return apiList
}